.data-panel {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.data-card {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 15px;
  padding: 1.5rem;
  color: white;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
}

.data-card h3 {
  margin: 0 0 1rem 0;
  font-size: 1.1rem;
  color: #64b5f6;
  text-align: center;
}

/* Summary Statistics */
.summary-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.stat-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 0.75rem;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.stat-label {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 0.25rem;
}

.stat-value {
  font-size: 1.1rem;
  font-weight: 600;
  color: white;
}

.stat-value.highlight {
  color: #4caf50;
  font-size: 1.2rem;
}

/* Loading State */
.loading-state {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 2rem;
  color: rgba(255, 255, 255, 0.7);
}

.pulse-dot {
  width: 8px;
  height: 8px;
  background: #64b5f6;
  border-radius: 50%;
  animation: pulse 1.5s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 1; transform: scale(1); }
  50% { opacity: 0.5; transform: scale(1.2); }
}

/* Whale Transactions */
.whales-list {
  max-height: 300px;
  overflow-y: auto;
}

.whale-item {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 0.75rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
}

.whale-item:hover {
  background: rgba(0, 0, 0, 0.3);
  border-color: rgba(100, 181, 246, 0.3);
  transform: translateX(5px);
}

.whale-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.whale-time {
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.7);
}

.whale-value {
  font-weight: 600;
  color: #4caf50;
  font-size: 1rem;
}

.whale-details {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.whale-usd {
  font-size: 0.9rem;
  color: rgba(255, 255, 255, 0.8);
}

.whale-hash {
  font-size: 0.8rem;
  color: #64b5f6;
  text-decoration: none;
  font-family: monospace;
  transition: color 0.3s ease;
}

.whale-hash:hover {
  color: #42a5f5;
  text-decoration: underline;
}

/* Blocks */
.blocks-list {
  max-height: 200px;
  overflow-y: auto;
}

.block-item {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 8px;
  padding: 0.75rem;
  margin-bottom: 0.5rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
}

.block-item:hover {
  background: rgba(0, 0, 0, 0.3);
  border-color: rgba(76, 175, 80, 0.3);
  transform: translateX(5px);
}

.block-info {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.block-time {
  font-size: 0.85rem;
  color: rgba(255, 255, 255, 0.7);
}

.block-height {
  font-size: 0.9rem;
  color: #4caf50;
  font-weight: 600;
}

.block-txs {
  font-size: 0.8rem;
  color: rgba(255, 255, 255, 0.8);
}

/* Empty States */
.empty-state {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  color: rgba(255, 255, 255, 0.5);
  font-style: italic;
}

/* Scrollbar Styling */
.whales-list::-webkit-scrollbar,
.blocks-list::-webkit-scrollbar {
  width: 6px;
}

.whales-list::-webkit-scrollbar-track,
.blocks-list::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
}

.whales-list::-webkit-scrollbar-thumb,
.blocks-list::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.3);
  border-radius: 3px;
}

.whales-list::-webkit-scrollbar-thumb:hover,
.blocks-list::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.5);
}
